// src/app/categoria/[slug]/page.js
'use client'; // <--- Adicione esta linha no topo do arquivo
import { useParams } from 'next/navigation';
import Link from 'next/link';
import products from '../../../data/products.json';
import categories from '../../../data/categories.json';

export default function CategoryPage() {
  const params = useParams();
  const slug = params.slug;

  // Encontrar a categoria pelo slug
  const category = categories.find((cat) => cat.slug === slug);

  if (!category) {
    return (
      <main className="min-h-screen p-8 bg-gray-100 text-center">
        <h1 className="text-3xl font-bold text-red-600">Categoria não encontrada</h1>
        <p className="mt-4 text-gray-600">A categoria solicitada não existe.</p>
      </main>
    );
  }

  // Filtrar produtos pela categoria
  const filteredProducts = products.filter(
    (product) => product.category === category.slug
  );

  return (
    <main className="min-h-screen p-8 bg-gray-100">
      {/* Cabeçalho da Categoria */}
      <header className="mb-8">
        <h1 className="text-3xl font-bold text-blue-600">{category.name}</h1>
        <p className="mt-2 text-gray-600">{category.description}</p>
      </header>

      {/* Lista de Produtos */}
      <section className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {filteredProducts.length > 0 ? (
          filteredProducts.map((product) => (
            <Link
              key={product.id}
              href={`/produto/${product.id}`}
              className="bg-white p-5 rounded shadow hover:shadow-lg transition duration-300 transform hover:-translate-y-1"
            >
              <div className="w-full h-48 mb-4 flex items-center justify-center overflow-hidden rounded">
                <img
                  src={product.image || '/images/default-product.jpg'}
                  alt={product.name}
                  className="object-cover w-full h-full"
                />
              </div>
              <h2 className="text-xl font-semibold">{product.name}</h2>
              <p className="text-sm text-gray-500 mt-1 line-clamp-2">
                {product.description}
              </p>
              <p className="mt-2 font-medium text-green-600">R$ {product.price.toFixed(2)}</p>
            </Link>
          ))
        ) : (
          <p className="text-center col-span-full text-gray-600">
            Nenhum produto encontrado nesta categoria.
          </p>
        )}
      </section>
    </main>
  );
}
